import React, {useState} from 'react';
import {useSelector, useDispatch} from 'react-redux';

import {Container, Cart, PurchaseContainer, Checkout, Item} from './styles';
import {
  removeProduct,
  decreaseProductQuantity,
  increaseProductQuantity,
} from '../../services/cart/actions';

const ShoppingCart = () => {
  const [toggle, setToggle] = useState(false);
  const dispatch = useDispatch();
  const products = useSelector(state => state.cart.products);

  const handleRemoveProduct = product => event => {
    dispatch(removeProduct(product));
  };

  const handleIncreaseProductQuantity = product => event => {
    dispatch(increaseProductQuantity(product));
  };

  const handleDecreaseProductQuantity = product => event => {
    dispatch(decreaseProductQuantity(product));
  };

  return (
    <Container toggle={toggle}>
      <Cart
        toggle={toggle}
        onClick={() => setToggle(currToggle => !currToggle)}
      />

      {toggle && (
        <React.Fragment>
          <PurchaseContainer>
            {products &&
              products.map(product => {
                const {id, imgUrl, name, quantity, price} = product;

                return (
                  <Item
                    key={id}
                    imgUrl={imgUrl}
                    prodName={name}
                    quantity={quantity}
                    totalPrice={price}
                    removeProduct={handleRemoveProduct(product)}
                    IncreaseProductQuantity={handleIncreaseProductQuantity(
                      product,
                    )}
                    DecreaseProductQuantity={handleDecreaseProductQuantity(
                      product,
                    )}
                  />
                );
              })}
          </PurchaseContainer>
          <Checkout totalPrice={300} />
        </React.Fragment>
      )}
    </Container>
  );
};

export {ShoppingCart};
